body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

*{font-family:sans-serif}.container{max-width:1000px}a{text-decoration:none;color:unset}a:hover{color:unset}.navbar{position:-webkit-sticky;position:sticky;top:0;z-index:1000000}.navbar .container>a{text-decoration:none;color:unset;font-family:"Montserrat";font-weight:600;font-size:1.8rem}.navbar .container .navbar-nav .nav-item{font-family:"Poppins"}.navbar .container .navbar-nav .nav-item a,.navbar .container .navbar-nav .nav-item button{text-decoration:none}.navbar .container .navbar-nav .nav-item .profile-link{display:flex;justify-content:center;align-items:center}.navbar .container .navbar-nav .nav-item .profile-link:hover{background:rgba(176,179,184,0.5)}.navbar .container .navbar-nav .nav-item .profile-link img{width:25px}.navbar .container .navbar-nav .nav-item .profile-link span{margin-left:10px;font-family:"Poppins"}.navbar .container .navbar-nav .nav-item .cta{color:#f1f0f0}.navbar .container .navbar-nav .dark-mode-switch{padding:5px;height:40px;width:40px;display:grid;place-items:center;border-radius:50%;cursor:pointer;margin-left:auto}.navbar .container .navbar-nav .dark-mode-switch .icon{font-size:1.5rem}.navbar_dark{border-bottom:2px solid #4e4f50;background-color:#18191a;color:#e4e6eb}.navbar_dark .container .navbar_brand{color:#e4e6eb}.navbar_dark .container .menu-btn .menu-btn-icon{color:#f1f0f0}.navbar_dark .container .navbar-nav li>*,.navbar_dark .container .navbar-nav li a{color:#e4e6eb}.navbar_dark .container .navbar-nav .dark-mode-switch{background-color:#242526}.navbar_dark .container .navbar-nav .dark-mode-switch .icon{color:#b0b3b8}.navbar_light{border-bottom:1px solid #242526;background-color:#f1f0f0;color:#18191a}.navbar_light .navbar_brand{color:#1e1e1e}.navbar_light .navbar-nav li>*,.navbar_light .navbar-nav li a{color:#242526}.navbar_light .navbar-nav .dark-mode-switch{background-color:#b0b3b8}.navbar_light .navbar-nav .dark-mode-switch .icon{color:#18191a}@media screen and (max-width: 576px){html{font-size:14px}.navbar-nav{width:100%;display:none !important}.navbar-nav .nav-item a,.navbar-nav .nav-item button{font-size:0.9rem}.expanded{display:flex !important;margin-top:15px}}

*{font-family:sans-serif}.container{max-width:1000px}a{text-decoration:none;color:unset}a:hover{color:unset}.app{min-height:100vh}.app_dark{background-color:#18191a}.app_light{background-color:#f1f0f0}

*{font-family:sans-serif}.container{max-width:1000px}a{text-decoration:none;color:unset}a:hover{color:unset}.loader,.loader:after{border-radius:50%;width:5rem;height:5rem}.loader{margin:60px auto;font-size:10px;position:relative;text-indent:-9999em;-webkit-transform:translateZ(0);transform:translateZ(0);-webkit-animation:load8 1.1s infinite linear;animation:load8 1.1s infinite linear}@-webkit-keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes load8{0%{-webkit-transform:rotate(0deg);transform:rotate(0deg)}100%{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}.loader-dark{border-top:1.1em solid rgba(176,179,184,0.2);border-right:1.1em solid rgba(176,179,184,0.2);border-bottom:1.1em solid rgba(176,179,184,0.2);border-left:1.1em solid #e4e6eb}.loader-light{border-top:1.1em solid rgba(78,79,80,0.2);border-right:1.1em solid rgba(78,79,80,0.2);border-bottom:1.1em solid rgba(78,79,80,0.2);border-left:1.1em solid #242526}

